.pagination_section{
  display: flex;
  justify-content: center;
  padding: 1rem 0;

  &_toggle{
    display: flex;
    column-gap: 1rem;
    align-items: center;

    &_number{
      font-size: 1.2rem;
    }
    &_previous, &_next{
      font-size: 1.2rem;
      cursor: pointer;
    }
  }
}